@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
body {
  margin: 0;
  font-family: Oswald, sans-serif;
}
img {
  max-width: 100%;
  object-fit: contain;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.button {
  font-family: Oswald, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  padding: 8px 44px;
  background: #68b738;
  display: block;
  text-align: center;
  border: none;
  text-transform: uppercase;
}
.button:hover {
  background: #5eaa2f;
}
.button:active {
  background: #5eaa2f;
  color: #ffffff75;
}
.button--secondary {
  font-family: Oswald, sans-serif;
  color: #444444;
  background: #F2F2F2;
  text-transform: uppercase;
}
.button--secondary:hover {
  background: #ebebeb;
}
.button--secondary:active {
  background: #ebebeb;
  color: #444444af;
}
.container {
  max-width: 1440px;
  padding: 0 20px;
  margin: 0 auto;
}
.container-sec {
  max-width: 1440px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 110px;
  }
}
.main-header {
  background-color: #ffffff;
  padding: 10px 20px;
  min-width: 66px;
}
.main-header__info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 56px;
}
.main-header__logo {
  margin: 0;
  padding-top: 2px;
}
.main-header__logo:hover {
  opacity: 0.3;
}
.main-header__logo:focus {
  opacity: 0.1;
}
@media (max-width: 767px) {
  .main-header {
    border-bottom: 1px solid #d9d9d9;
  }
  .main-header__info {
    justify-content: space-between;
  }
  .main-header__toggle {
    margin: 0;
    padding: 0;
    z-index: 2;
    display: block;
    width: 24px;
    min-height: 16px;
    cursor: pointer;
    border: none;
    position: relative;
    background-color: transparent;
    margin-left: 9px;
    margin-bottom: 5px;
  }
  .main-header__toggle--nojs {
    display: none;
  }
  .main-header__toggle--closed::before {
    width: 24px;
    height: 24px;
    position: absolute;
    content: "";
    background-image: url("../img/menu.svg");
    background-repeat: no-repeat;
    top: 0;
    right: 0;
  }
  .main-header__toggle--opened {
    display: block;
  }
  .main-header__toggle--opened::before {
    width: 19px;
    height: 19px;
    position: absolute;
    content: "";
    background-image: url("../img/nav-opened.svg");
    top: 0;
    right: 0;
    background-repeat: no-repeat;
  }
  .logo-img {
    height: 38px;
  }
  .logo-slogan {
    width: 101px;
    height: 18px;
  }
}
@media (min-width: 768px) {
  .main-header {
    padding: 0 30px;
    padding-top: 26px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    flex-grow: 1;
    margin: 0;
  }
  .main-header__info {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    gap: 12px;
  }
  .logo-img {
    width: 60px;
    height: 50px;
  }
  .logo-slogan {
    width: 101px;
    height: 19px;
  }
  .main-header__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: nowrap;
    gap: 33px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding: 55px 0;
    padding-bottom: 0;
    max-width: 1220px;
    margin: auto;
    gap: 539px;
    justify-content: start;
  }
  .logo-img {
    height: 59px;
    width: 70px;
  }
  .logo-slogan {
    width: 118px;
    height: 21px;
  }
  .main-header--active {
    position: absolute;
    z-index: 3;
    background-color: transparent;
    color: #000000;
    width: 100%;
    left: calc(50vw - 1220px / 2);
  }
  .main-header__info {
    gap: 14px;
  }
}
.main-nav--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}
.main-nav__list {
  list-style-type: none;
  padding: 0;
  padding-top: 12px;
  margin: 0;
}
.main-nav__item {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  font-family: Oswald, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  padding-top: 29px;
  padding-bottom: 20px;
  border-top: 1px solid #e6e6e6;
}
.main-nav__item:last-child {
  padding-top: 26px;
  padding-bottom: 22px;
}
.main-nav__link {
  color: inherit;
}
.main-nav__link--current {
  color: #000000;
}
@media (max-width: 767px) {
  .main-nav {
    position: absolute;
    width: 100%;
    background-color: #ffffff;
    left: 0;
    z-index: 8;
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    color: #000000;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__link:hover {
    color: #00000079;
  }
  .main-nav__link:focus {
    color: #F2F2F2;
  }
  .main-nav__link--current {
    position: relative;
  }
  .main-nav__link--current::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -8px;
    left: 0;
    height: 2px;
    background: #68b738;
  }
  .main-nav__item {
    border: none;
    padding-top: 4px;
    padding-left: 5px;
  }
  .main-nav__item:last-child {
    padding-top: 6px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    color: #000000;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__link--active {
    color: #ffffff;
  }
  .main-nav__link--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -8px;
    left: 0;
    background: #ffffff;
  }
  .main-nav__link--active:hover {
    color: #ffffff57;
  }
  .main-nav__link--active:focus {
    color: #ffffffb4;
  }
  .main-nav__link--current {
    position: relative;
  }
}
.main-container__wrapper {
  margin: 0 20px;
}
@media (min-width: 768px) {
  .main-container__wrapper {
    margin: 0 30px;
  }
}
@media (min-width: 1440px) {
  .main-container__wrapper {
    margin: auto;
    margin-top: 188px;
  }
  .container__secondary {
    margin: 0;
  }
}
