@tablet-width: 768px;
@desktop-width: 1440px;

.container {
  max-width: 1440px;
  padding: 0 20px;
  margin: 0 auto;
}

.container-sec {
  max-width: 1440px;
  margin: 0 auto;
}

@media (min-width: @tablet-width) {
  .container {
    padding: 0 30px;
  }
}

@media (min-width: @desktop-width) {
  .container {
    padding: 0 110px;
  }
}
